export default {
	data() {
		return {
			user: {
				realName: '',
				weixin: '',
				telephone: '',
				qq: '',
				picture: '',
				img:'../../static/images/homeed.png'
			}
		}
	},
	onShow() {
		let _this = this;
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				if(res.data){
					_this.user = res.data;
					_this.user.img = '../../static/images/homeed.png';
				}
		    }
		});
	},
	methods: {
		// 登录事件
		login() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		logout() {
			let _this = this;
			uni.setStorage({
			    key: 'userInfo',
			    data: '',
			    success: function () {
					_this.user.account='';
			        uni.showToast({
			            title: '退出成功',
						icon: 'none',
			            duration: 2000
			        });
					uni.setStorage({
					    key: 'token',
					    data: ''
					});
			    }
			});
		},
		revisePwd(){
			uni.navigateTo({
				url: '../revisePwd/revisePwd'
			});
		},
		reviseUser(){
			uni.navigateTo({
				url: '../reviseUser/reviseUser',
			});
		}
	}
}
